
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Msg from 'vux/src/components/msg/index.vue'

import {mapState} from 'vuex'

export default {
name: 'resetSetting',
directives: {
TransferDom
},
components: {
XInput,
ViewBox,
XButton,
XHeader,
Box,
Divider,
Group,
Cell,
Msg,
CellBox,
PopupHeader,
Popup
},
mounted: function () {
this.newNickName = this.currentUser.nickName
},
data () {
return {
msg: 'resetSetting page',
showEdit: false,
newNickName: '',
setRealName: '',
setIdentityNo: '',
editModel: {
title: '',
currentEdit: 'nickName'
}
}
},
computed: {
...mapState({
currentUser: state => state.currentUser
})
},
methods: {
nickNameReset () {
this.editModel.title = ''
this.editModel.currentEdit = 'nickName'
this.showEdit = true




},
realNameReset () {
this.editModel.title = ''
this.editModel.currentEdit = 'realName'
this.showEdit = true
},
doReset () {

if (this.editModel.currentEdit === 'realName' && this.currentUser.userStatus === true) {
this.showEdit = false
}
if (this.editModel.currentEdit === 'realName' && this.currentUser.userStatus === false) {
console.log('')
this.$api.userApi.initUserInfo(this.currentUser.userId, this.setRealName, this.setIdentityNo).then(res => {
if (res.data.success) {
this.updateUserInfo()
this.showEdit = false
this.$vux.toast.show({
type: 'success',
text: ''
})
}
})
}

if (this.editModel.currentEdit === 'nickName') {
if (this.newNickName === this.currentUser.nickName) {
this.showEdit = false
} else {
this.$api.userApi.updateNickName(this.currentUser.userId, this.currentUser.nickName, this.newNickName).then(res => {
if (res.data.success) {
this.updateUserInfo()
this.showEdit = false
this.$vux.toast.show({
type: 'success',
text: ''
})
}
})
}
}
},
doRequest (requestOptions) {
this.doPost(requestOptions).then(result => {
if (result.success) {
this.updateUserInfo()
this.showEdit = false
this.$vux.toast.show({
type: 'success',
text: ''
})
} else {
this.$vux.toast.show({
type: 'text',
text: result.message,
width: '10em'
})
}
})
},
updateUserInfo () {
this.$api.userApi.getUserInfoById(this.currentUser.userId).then(res => {
if (res.data.success) {
this.$store.commit('updateCurrentUser', res.data.data)
}
})
}
}
}



