
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import XTable from 'vux/src/components/x-table/index.vue'

import Coin from './GetCoin'
export default {
mounted: function () {
this.init()
},
name: 'wakuang',
components: {
Coin,
MarqueeItem,
XTable
},
data () {
return {
chengjiudian: 0,
gonggao: '',
totalPeople: 10000,
coins: [],
paihangList: []
}
},
methods: {
init () {
console.log('Init ')
let user = JSON.parse(window.localStorage.getItem('User'))
console.log(user)
this.chengjiudian = user.calculateValue
this.getCoinRequest(user.userId)
this.getPaihangBang()
},
doGetCoin (iscLID) {
console.log('ID: ' + iscLID)
this.$api.calculateApi.checkCalculate(iscLID).then(result => {
if (result.data.success) {
console.log('')
} else {
this.$vux.toast.show({
type: 'text',
text: result.data.message
})
}
})
},
mingciCSS: function (item) {
if (item === 1) {
return 'first'
} else if (item === 2) {
return 'second'
} else if (item === 3) {
return 'third'
} else {
return 'other'
}
},
getCoinRequest (userId) {
this.$api.calculateApi.getCalculateLog(userId).then(result => {
console.log(result)
if (result.data.success) {
this.coins = result.data.data
} else {
this.$vux.toast.show({
type: 'cancel',
text: result.message
})
}
})
},
getPaihangBang () {
this.$api.calculateApi.getCalculateTop100().then(result => {
if (result.data.success) {
this.paihangList = result.data.data
} else {
this.$vux.toast.show({
type: 'text',
text: result.data.message
})
}
})
}
},
computed: {}
}



